@font-face {
  font-family: Komikasl;
  src: url(../fonts/KOMIKASL.ttf);
}
@font-face {
  font-family: BoldlyMissy;
  src: url(../fonts/BoldlyMissy.otf);
}
body {
  font-family: Komikasl, sans-serif;
  -webkit-font-smoothing: antialiased;
  background-color: #363333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

p {
  font-size: 1.1rem;
  line-height: 150%;
  margin: 0 0 1.75rem;
  color: #525c65;
  font-weight: 400;
}

blockquote {
  font-size: 1.2rem;
  font-style: italic;
  border-left: 3px solid #f78f5d;
  padding-left: 2rem;
  margin: 0 0 1.75rem;
  color: #333333;
}
blockquote p {
  color: #333333;
  font-size: 1.2rem;
}

::-webkit-input-placeholder {
  color: #fffefb;
}

::-moz-placeholder {
  color: #fffefb;
}

input[type=email], input[type=text], input[type=tel], textarea {
  border: none;
  border-radius: 0.3rem;
  outline: none;
}

cite {
  color: #949494;
}

.wrapper {
  width: 1440px;
  max-width: 90%;
  margin: 0.5rem auto;
}

.button_back {
  color: #fffefb;
  font-size: 1.1rem;
  text-decoration: none;
  font-style: italic;
  display: inline-block;
  margin: 0.7rem 1rem;
  padding-left: 0.5rem;
  font-weight: 700;
}
.button_back:hover {
  text-decoration: underline;
}

.cf::before, .cf::after {
  content: "";
  display: table;
}

.cf::after {
  clear: both;
}

.top-nav {
  padding: 1.2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-family: BoldlyMissy, sans-serif;
  background-color: #171515;
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #bfbbb2;
}

.menu-title > a {
  font-size: calc(1.395rem + 1.74vw);
  color: #d18657;
  margin-left: 1rem;
  text-decoration: none;
}
@media (min-width: 1200px) {
  .menu-title > a {
    font-size: 2.7rem;
  }
}

.menu {
  display: flex;
  flex-direction: row;
  list-style-type: none;
  margin: 0;
  padding: 0;
  z-index: 1;
}

.menu > li {
  margin: 0 1rem;
  overflow: hidden;
}

.menu > li > a {
  color: #bfbbb2;
  text-decoration: none;
  font-size: calc(1.325rem + 0.9vw);
}
@media (min-width: 1200px) {
  .menu > li > a {
    font-size: 2rem;
  }
}
.menu > li > a:hover {
  color: #d18657;
}

