.carousel__control {
  height: 24px;
  width: 24px;
  margin-top: -10px;
  top: 50%;
  position: absolute;
  display: block;
  cursor: pointer;
  border-width: 5px 5px 0 0;
  border-style: solid;
  border-radius: 5px;
  border-color: #bfbbb2;
  opacity: 1;
  outline: 0;
  z-index: 3;
}

